  color: #0537ec;
  outline: none;
}

/**
  * Headings & corresponding classes
  */
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 1.6em 0 0.5em;
  font-weight: 400;
  line-height: 1.35;
  color: #333;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
}

