  font-size: 1em;
}

small, h6, .h6 {
  font-size: 0.83333em;
}

/**
  * Blockquotes with a footer
  */
blockquote {
  border-left: 2px solid #333;
  margin: 0 0 1.3em 0;
  padding: 0 1em;
}

blockquote ol:last-of-type,
blockquote p:last-of-type,
blockquote ul:last-of-type {
  margin-bottom: 0;
}

blockquote footer {
  margin-top: 0.5em;
  font-size: 0.83333em;
  color: #898989;
}

blockquote footer:before {
  content: '\2014 \00A0';
}

blockquote footer cite {
  font-style: italic;
}

